
Template.__checkName("buttons");
Template["buttons"] = new Template("Template.buttons", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("idle"));
  }, function() {
    return [ "\n        ", Blaze.If(function() {
      return Spacebars.call(view.lookup("cancelled"));
    }, function() {
      return [ "\n            ", HTML.DIV({
        "class": function() {
          return Spacebars.mustache(view.lookup("class"), "cancelledButton");
        }
      }, "\n                ", HTML.I({
        "class": function() {
          return Spacebars.mustache(view.lookup("class"), "cancelledButtonIcon");
        }
      }), "\n                ", HTML.SPAN(Blaze.View("lookup:ut9n", function() {
        return Spacebars.mustache(view.lookup("ut9n"), "cancelled");
      })), "\n            "), "\n        " ];
    }, function() {
      return [ "\n            ", Blaze.If(function() {
        return Spacebars.call(view.lookup("waiting"));
      }, function() {
        return [ "\n                ", Blaze.If(function() {
          return Spacebars.call(view.lookup("removeFromQueue"));
        }, function() {
          return [ "\n                    ", HTML.DIV({
            type: "submit",
            "class": function() {
              return Spacebars.mustache(view.lookup("class"), "removeButton");
            }
          }, "\n                        ", HTML.I({
            "class": function() {
              return Spacebars.mustache(view.lookup("class"), "removeButtonIcon");
            }
          }), "\n                        ", HTML.SPAN(Blaze.View("lookup:ut9n", function() {
            return Spacebars.mustache(view.lookup("ut9n"), "remove");
          })), "\n                    "), "\n                " ];
        }, function() {
          return [ "\n                    ", Blaze.Unless(function() {
            return Spacebars.call(view.lookup("autoStart"));
          }, function() {
            return [ "\n                        ", HTML.DIV({
              type: "submit",
              "class": function() {
                return Spacebars.mustache(view.lookup("class"), "startButton");
              }
            }, "\n                            ", HTML.I({
              "class": function() {
                return Spacebars.mustache(view.lookup("class"), "startButtonIcon");
              }
            }), "\n                            ", HTML.SPAN(Blaze.View("lookup:ut9n", function() {
              return Spacebars.mustache(view.lookup("ut9n"), "upload");
            })), "\n                        "), "\n                    " ];
          }), "\n                " ];
        }), "\n            " ];
      }, function() {
        return [ "\n                ", HTML.DIV({
          "class": function() {
            return Spacebars.mustache(view.lookup("class"), "doneButton");
          }
        }, "\n                    ", HTML.I({
          "class": function() {
            return Spacebars.mustache(view.lookup("class"), "doneButtonIcon");
          }
        }), "\n                    ", Blaze.View("lookup:ut9n", function() {
          return Spacebars.mustache(view.lookup("ut9n"), "done");
        }), "\n                "), "\n            " ];
      }), "\n        " ];
    }), "\n    " ];
  }, function() {
    return [ "\n        ", HTML.DIV({
      "class": function() {
        return Spacebars.mustache(view.lookup("class"), "cancelButton");
      }
    }, "\n            ", HTML.I({
      "class": function() {
        return Spacebars.mustache(view.lookup("class"), "cancelButtonIcon");
      }
    }), "\n            ", HTML.SPAN(Blaze.View("lookup:ut9n", function() {
      return Spacebars.mustache(view.lookup("ut9n"), "cancel");
    })), "\n        "), "\n    " ];
  });
}));
