
Template.__checkName("upload_bootstrap");
Template["upload_bootstrap"] = new Template("Template.upload_bootstrap", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      type: Spacebars.call("bootstrap"),
      formData: Spacebars.call(view.lookup("formData")),
      fileTypes: Spacebars.call(view.lookup("fileTypes")),
      multiple: Spacebars.call(view.lookup("multiple")),
      callbacks: Spacebars.call(view.lookup("callbacks")),
      autoStart: Spacebars.call(view.lookup("autoStart")),
      contentType: Spacebars.call(view.lookup("contentType"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("upload"));
  });
}));

