Template.__checkName("upload");
Template["upload"] = new Template("Template.upload", (function() {
  var view = this;
  return HTML.FORM({
    method: "POST",
    enctype: "multipart/form-data"
  }, "\n\n        ", HTML.DIV({
    "class": "uploadPanel"
  }, "\n            ", HTML.Raw("<!-- The fileinput-button span is used to style the file input field as button -->"), "\n\n            ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("class"), "upload");
    }
  }, "\n                ", Blaze.View("lookup:ut9n", function() {
    return Spacebars.mustache(view.lookup("ut9n"), "browse");
  }), HTML.Raw("&hellip;"), " ", HTML.INPUT({
    type: "file",
    "class": "jqUploadclass",
    multiple: function() {
      return Spacebars.mustache(view.lookup("multiple"));
    },
    "data-form-data": function() {
      return Spacebars.mustache(view.lookup("submitData"));
    },
    accept: function() {
      return Spacebars.mustache(view.lookup("fileTypes"));
    }
  }), "\n            "), "\n\n            ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("class"), "progressOuter");
    }
  }, "\n                ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("class"), "progressInner");
    }
  }, "\n                    ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("class"), "progressBar");
    },
    role: "progressbar",
    "aria-valuemin": "0",
    "aria-valuemax": "100",
    style: function() {
      return Spacebars.mustache(view.lookup("progress"));
    }
  }, "\n                        ", HTML.Raw("&nbsp;"), "\n                    "), "\n                    ", HTML.DIV({
    "class": "progress-label"
  }, "\n                        ", Blaze.View("lookup:infoLabel", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("infoLabel")));
  }), "\n                    "), "\n                "), "\n            "), "\n\n            ", Blaze._TemplateWith(function() {
    return {
      type: Spacebars.call(view.lookup("type")),
      state: Spacebars.call(view.lookup("buttonState")),
      uploadContext: Spacebars.call(view.lookup("uploadContext")),
      autoStart: Spacebars.call(view.lookup("autoStart"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("buttons"));
  }), "\n        "), "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("showQueue"));
  }, function() {
    return [ "\n            ", HTML.DIV({
      "class": "panel panel-default",
      style: "margin-top: 1px; background: #efefef"
    }, "\n                ", HTML.DIV({
      "class": "panel-body"
    }, "\n                    ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("queueItems"));
    }, function() {
      return [ "\n                        ", Blaze._TemplateWith(function() {
        return {
          type: Spacebars.call(Spacebars.dot(view.lookup(".."), "type")),
          uploadContext: Spacebars.call(view.lookup("uploadContext")),
          item: Spacebars.call(view.lookup("."))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("queueItem"));
      }), "\n                    " ];
    }), "\n                "), "\n            "), "\n        " ];
  }), "\n    ");
}));
