
Template.__checkName("dropzone");
Template["dropzone"] = new Template("Template.dropzone", (function() {
  var view = this;
  return HTML.FORM({
    method: "POST",
    enctype: "multipart/form-data"
  }, "\n        ", HTML.INPUT({
    type: "file",
    "class": "jqUploadclass",
    multiple: "true",
    "data-content-category": function() {
      return Spacebars.mustache(view.lookup("contentType"));
    },
    "data-form-data": function() {
      return Spacebars.mustache(view.lookup("submitData"));
    },
    accept: function() {
      return Spacebars.mustache(view.lookup("fileTypes"));
    },
    style: "display: none"
  }), "\n        ", HTML.DIV({
    "class": "jqDropZone fade"
  }, Blaze.View("lookup:infoLabel", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("infoLabel")));
  })), "\n    ");
}));
